body[data-theme="dark"] {  

   @import '../src/scss/dark-slider.scss';

   .slider-tab {
      background-color: #8C8C8C;
   }

   .title-styles, .intro_text {
      color: white;
   }

   .header-icon {
      color: #3C342C;
      opacity: 0.8;
   }
   .header{
      
      .navigation a{
         color: white;
         text-decoration: solid;
     }
   }

   .slider-image {
      border: 5px solid #8C8C8C;
   }

   .language {
      background-color: #494949;
   }

   .link-href {
      color: white;
   }

   .project-date {
      background-color: #918e97;
      color: white;
   }

   header {
      background-color: #494949;
      h1 {
         color: white;
      }
   }

   .portfolio-body{
      background-color: #242020;
   }
   #section0 {
      background: #242020;
      .clouds{
         display: none;
      }
      .sun{
         background: #ffff;
         
          box-shadow: 0px 0px 40px 15px #fff;  
         -webkit-animation: glowing2 1s ease-in-out infinite alternate;
         -moz-animation: glowing2 1s ease-in-out infinite alternate;
         animation: glowing2 1s ease-in-out infinite alternate;
      }
   }
   

   #about {
      background-color: #242020;
      h1 span {
         color: white;
      }
      .polaroid span {
         background:  #242020;
      }
      .card {
         background: #242020;
         color: white;
      }
   }

   #portfolio {
      background: #5b5b5b;
      .section-title {
         color: white !important;
      }
      .foto div {
         background: #6d6d6d;
      }
      .project-title-settings {
         color: white;
      }
   }

   .vertical-timeline {
      color: white;
   }

   .vertical-timeline-element-content {
      background: #5B8FB9 !important;
   }
}

